<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title> </v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark class="mb-2" v-on="on"> </v-btn>
        </template>
        <v-card>
          <v-card-title justify-center>
            <span class="text-md-center">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.service_name" label=" "></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-select
                    :items="serviceTypes"
                    v-model="editedItem.service_type"
                    label=" "
                  ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.origin_price" label=""></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.discount_rate" label=" "></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.discount_price" label="  "></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="save"></v-btn>
            <v-btn color="red darken-1" flat @click="close"></v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="services"
      class="elevation-1"
    >
      <template v-slot:items="props">
        <td class="text-xs-left px-5">{{ props.item.service_name }}</td>
        <td class="text-xs-left px-5">{{ props.item.service_type }}</td>
        <td class="text-xs-left px-5">{{ props.item.origin_price }}</td>
        <td class="text-xs-left px-5">{{ props.item.discount_price }}</td>
        <td class="text-xs-left px-5">{{ props.item.discount_rate }}</td>
        <td class="justify-center layout px-0">
          <v-icon
            small
            class="ml-2"
            @click="editItem(props.item)"
          >
            edit
          </v-icon>
          <v-divider
            class="mx-2"
            inset
            vertical
          ></v-divider>
          <v-icon
            small
            @click="deleteItem(props.item)"
          >
            delete
          </v-icon>
        </td>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
</template>
<script>
  export default {
    data: () => ({
      dialog: false,
      serviceTypes: [ '', '', ''],
      headers: [
        {
          text: ' ',
          align: 'left',
          sortable: false,
          value: 'service_name'
        },
        { text: ' ', value: 'service_type', sortable: false },
        { text: ' ', value: 'origin_price', sortable: false },
        { text: '  ', value: 'discount_price', sortable: false },
        { text: ' ', value: 'discount_rate', sortable: false },
        { text: '', value: 'name', sortable: false }
      ],
      services: [],
      editedIndex: -1,
      editedItem: {
        service_name: '',
        service_type: '',
        origin_price: 0,
        discount_price: 0,
        discount_rate: 0
      },
      defaultItem: {
        service_name: '',
        service_type: 0,
        origin_price: 0,
        discount_price: 0,
        discount_rate: 0
      }
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? ' ' : ' '
      },
      price_after_discount () {
        return this.editedItem.origin_price 
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.services = [
          {
            service_name: 'Frozen Yogurt',
            service_type: 159,
            origin_price: 6.0,
            discount_price: 24,
            discount_rate: 4.0
          },
          {
            service_name: 'Ice cream sandwich',
            service_type: 237,
            origin_price: 9.0,
            discount_price: 37,
            discount_rate: 4.3
          }
        ]
      },

      editItem (item) {
        this.editedIndex = this.services.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.services.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.services.splice(index, 1)
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.services[this.editedIndex], this.editedItem)
        } else {
          this.services.push(this.editedItem)
        }
        this.close()
        // console.log(this.services)
      }
    }
  }
</script>