<template>
    <v-form>
        <v-card class="ma-4 grey lighten-5" flat>
            <v-card-title>
                <v-spacer></v-spacer>
                <span class="subheading cairo">  </span>
                <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="centerName"
                            :rules="centerNameRules"
                            :counter="20"
                            required
                            label="  *"
                            class="ma-4"
                            @change="changeName"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="centerAddress"
                            :rules="centerAddressRules"
                            :counter="20"
                            required
                            label=" *"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-select
                        v-model="select"
                        :items="citys"
                        :rules="[v => !!v || ' ']"
                        label=" *"
                        required
                        class="ma-4"
                        ></v-select>
                    </v-flex>

                    <v-flex
                    xs12
                    md3
                    >
                    <v-text-field
                        v-model="centerWebsite"
                        :counter="20"
                        label="  ()"
                        class="ma-4"
                    ></v-text-field>
                    </v-flex>

                    <v-flex
                    xs12
                    md3
                    >
                        <v-text-field
                            v-model="centerFacebook"
                            :rules="centerFacebookRules"
                            :counter="200"
                            label="   ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md3
                    >
                        <v-text-field
                            v-model="centerTwitter"
                            :rules="centerTwitterRules"
                            :counter="200"
                            label="  ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md3
                    >
                        <v-text-field
                            v-model="centerLinkedin"
                            :rules="centerLinkedinRules"
                            :counter="200"
                            label="  ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md12
                    >
                        <v-textarea
                            class="ma-4"
                            v-model="centerDiscription"
                            label="  *"
                        ></v-textarea>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="success" @click="getCenterData"><span class="cairo"> </span></v-btn>
                <v-btn flat color="error" @click="reset"><span class="cairo"> </span></v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-form>
</template>

<script>
export default {
    name: 'CenterBasicInfo',
    data(){
        return {
            valid: false,
            centerName: '',
            centerNameRules: [
                v => !!v || ' ',
                v => v.length <= 20 || '      20 '
            ],
            centerAddress: '',
            centerAddressRules: [
                v => !!v || ' ',
                v => v.length <= 20 || '      20 '
            ],
            CenterCity: '',
            centerCityRules: [
                v => !!v || '  ',
                v => v.length <= 20 || '      20 '
            ],
            centerWebsite: '',
            centerWebsiteRules: [
                v => !!v || '   ',
                v => v.length <= 20 || '      20 '
            ],
            centerDiscription: '',
            centerDiscriptionRules: [
                v => !!v || '   ',
                v => v.length <= 200 || '      200 '
            ],
            centerFacebook: '',
            centerFacebookRules: [
                v => !!v || '   ',
                v => v.length <= 200 || '      200 '
            ],
            centerLinkedin: '',
            centerLinkedinRules: [
                v => !!v || '   ',
                v => v.length <= 200 || '      200 '
            ],
            centerTwitter: '',
            centerTwitterRules: [
                v => !!v || '   ',
                v => v.length <= 200 || '      200 '
            ],
            select: null,
            citys: [
                '',
                ' ',
                '',
                '',
            ], 
        }
    },
    methods: {
        getCenterData(){
            const centerData = [
                this.centerName,
                this.CenterCity,
                this.centerAddress,
                this.centerWebsite,
                this.centerDiscription,
                this.centerFacebook,
                this.centerTwitter,
                this.centerLinkedin
                ]
            this.$emit('center', centerData)

        },
        submit () {
            // const center = [
            //     this.centerName,
            //     this.CenterCity,
            //     this.centerAddress,
            //     this.centerWebsite,
            //     this.centerDiscription,
            //     this.centerFacebook,
            //     this.centerTwitter,
            //     this.centerLinkedin,
            // ]
        },
        changeName () {
            // console.log(this.centerName)
        },
        reset () {
            this.centerName = '';
            this.CenterCity = '';
            this.centerAddress = '';
            this.centerWebsite = '';
            this.centerDiscription = '';
            this.centerFacebook = '';
            this.centerTwitter = '';
            this.centerLinkedin = '';
        }
    }
}
</script>
