<template>
    <v-form>
        <v-card flat class="ma-4 grey lighten-5">
            <v-card-title>
                <v-spacer></v-spacer>
                <span class="subheading cairo">   </span>
                <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="responserName"
                            :rules="responserNameRules"
                            :counter="20"
                            required
                            label="  *"
                            class="ma-4"
                            @change="change"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="responserPhone"
                            :rules="responserPhoneRules"
                            :counter="15"
                            required
                            label="  *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                    <v-text-field
                        v-model="responserEmail"
                        :counter="15"
                        label="  ()"
                        class="ma-4"
                    ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="responserFacebook"
                            :rules="responserFacebookRules"
                            :counter="200"
                            label="   ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="responserTwitter"
                            :rules="responserTwitterRules"
                            :counter="200"
                            label="  ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="responserLinkedin"
                            :rules="reponserLinkedinRules"
                            :counter="200"
                            label="  ()"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="success" @click="submit"><span class="cairo"> </span></v-btn>
                <v-btn flat color="red" @click="reset"><span class="cairo"> </span></v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-form>
</template>

<script>
import ResponserServec from '../../services/ResponserServec'

export default {
    name: 'AddCenterResponserInfo',
    data(){
        return {
            valid: false,
            responserName: '',
            responserNameRules: [
                v => !!v || '   ',
                v => v.length <= 200 || '      20 '
            ],
            responserPhone: '',
            responserPhoneRules: [
                v => !!v || '    ',
                v => v.length <= 15 || '      15 '
            ],
            responserEmail: '',
            responserFacebook: '',
            responserFacebookRules: [
                v => !!v || '  ',
                v => v.length <= 200 || '      20 '
            ],
            responserLinkedin: '',
            reponserLinkedinRules: [
                v => !!v || '  ',
                v => v.length <= 200 || '      20 '
            ],
            responserTwitter: '',
            responserTwitterRules: [
                v => !!v || '  ',
                v => v.length <= 200 || '      20 '
            ],
        }
    },
    methods: {
        submit () {
            const responser = {
                name :  this.responserName,
                phone : this.responserPhone,
                email : this.responserEmail,
                twitter : this.responserTwitter,
                facbocok : this.responserFacebook,
                linkedin : this.responserLinkedin,
            }
            this.$emit('responser' , responser)
            ResponserServec.create(responser)
            // .then(res => { 
            //     console.log(res.data)
            //     this.reset();
            // })
            // .catch(error => console.log(error))
        },
        change () {
            // console.log(this.responserName)
        },
        reset () {
            this.responserName = '';
            this.responserPhone = '';
            this.responserEmail = '';
            this.responserTwitter = '';
            this.responserFacebook = '';
            this.responserLinkedin = '';
        }
    }
}
</script>